.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


table {
  width: 100%;
  min-width: 300px;
  /* Ensure minimum width even if screen is too small */
  border-collapse: collapse;
}

table,
th,
td {
  border: 1px solid #ccc;
}

th,
td {
  padding: 8px;
}

.model-col {
  width: 200px;
  text-align: left;
}

.globalAverage-col {
  width: 115px;
}

.category-checkboxes {
  margin-bottom: 10px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  margin: 0 auto;
  justify-content: center;
}

.category-checkboxes label {
  margin-right: 10px;
  font-size: 16px;
  color: #007bff;
  cursor: pointer;
}


.table-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  /* Align items at the top */
  flex-direction: column;
  max-width: 80%;
  width: 100%;
  /* Take the full width up to 80% of the parent container */
  margin: 10px auto;
  /* Center the table-container horizontally */
  padding: 10px;
  overflow-x: auto;
  /* Enable horizontal scrolling */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  /* Optional: adds shadow for better visibility */
}

.scrollable-table {
  margin: 0 auto;
  /* Center the scrollable-table horizontally */
}

.category-group {
  margin-right: 10px;
  margin-left: 10px;
  width: 250px;
  display: flex;
  justify-content: center;
  /* Center content within each category-group */
  flex-direction: column;
  /* Ensure the labels stack vertically */
}

.category-checkboxes label {
  margin-right: 10px;
  margin-left: 10px;
  font-size: 16px;
  color: #007bff;
  cursor: pointer;
  display: flex;
  align-items: center;
  /* Align checkbox and text vertically */
}

.category-checkboxes input[type="checkbox"] {
  margin-right: 8px;
  /* Add space between checkbox and text */
}

.App-header {
  font-size: calc(10px + 2vmin);
}

@media (max-width: 600px) {
  .App-header {
    font-size: calc(8px + 2vmin);
  }

  .table-container {
    max-width: 100%;
    margin: 10px;
  }

  .category-checkboxes {
    flex-direction: column;
    align-items: center;
  }

  .category-group {
    width: 100%;
    margin: 5px 0;
  }

  .table {
    min-width: 0;
  }

  th,
  td {
    padding: 6px;
  }
}

/* Style adjustments for medium screens */
@media (min-width: 601px) and (max-width: 1024px) {
  .App-header {
    font-size: calc(9px + 2vmin);
  }

  .table-container {
    max-width: 90%;
  }
}

@media (max-width: 400px) {
  .App .title.is-1 {
    font-size: 2.75rem !important;
  }
  .App .is-size-5 {
    font-size: 1rem !important;
  }
}


.category-group {
  margin: 5px 0;
}

